.projects-page-container {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0; /* Remove padding from the container */
}

.section-title {
  font-size: 2rem;
  text-align: center;
  color: #333;
  font-weight: bold;
}

.featured-projects {
  background-color: #f0f0f0;
  border-radius: 8px;
}

.all-projects {
  background-color: #f9f9f9;
  border-radius: 8px;
}

.projects-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 20px;
  margin-top: 0; /* Remove margin from the container */
}

.project-tile {
  position: relative;
  background: #fff; /* Plain white background for the tile */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  cursor: pointer;
  transition: box-shadow 0.2s ease-in-out;
  background: linear-gradient(-45deg, #4a90e2, #7854e2, #e64a8c, #e6b34a);
  background-size: 400% 400%;
  animation: gradientAnimation 10s ease infinite;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.project-tile:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.project-image {
  width: 100%;
  height: 20vw;
  object-fit: cover;
  object-position: center;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  transition: opacity 0.2s ease-in-out;
}

.project-tile .project-title-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.7);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  opacity: 0;
  transform: translateY(-100%);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.project-tile:hover .project-title-container {
  opacity: 1;
  transform: translateY(0);
}

.project-title {
  font-size: 1.5rem;
  color: #fff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.6);
  margin: 0;
  padding: 8px;
}

.project-description {
  font-size: 1rem;
  text-align: center;
  color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: translateY(-100%);
  transition: opacity 0.3s ease-in-out, visibility 0s 0.3s;
}

.project-tile:hover .project-description {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  transition-delay: 0s;
}

/* Optional: Add a transition to the dimming effect of the image */
.project-tile .project-image:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
}

.project-tile:hover .project-image:before {
  opacity: 1;
}
